@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;0,700;0,800;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box}.icon{font-family:'Material Icons', serif;font-style:normal;display:inline-block;vertical-align:middle;line-height:1;text-transform:none;letter-spacing:normal;word-wrap:normal;white-space:nowrap;direction:ltr;-webkit-font-smoothing:antialiased;text-rendering:optimizeLegibility;-moz-osx-font-smoothing:grayscale;font-feature-settings:'liga'}:focus{outline-style:none;outline-width:0px !important;outline-color:none !important}body{font-family:'Montserrat', sans-serif;font-size:1em;font-weight:300;line-height:1.5;color:#000;background:#f1f2b5;background:linear-gradient(to right, #135058, #f1f2b5);background-repeat:no-repeat !important;background-size:cover}.icon{cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none}#root{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-align-content:center;align-content:center;-webkit-justify-content:center;justify-content:center;height:100vh}.App{display:-webkit-flex;display:flex;width:762px;-webkit-align-items:center;align-items:center;-webkit-align-content:center;align-content:center;-webkit-justify-content:center;justify-content:center;-webkit-flex-flow:column nowrap;flex-flow:column nowrap;background-color:#ffffff;max-width:762px;max-height:98vh;border:1px solid rgba(255,255,255,0.18);box-shadow:0 8px 32px 0 rgba(31,38,135,0.37)}@media only screen and (min-width: 0px) and (max-width: 767px){.App{border-radius:0;width:100%;height:100%;max-width:100%;max-height:100%;box-shadow:none}}

.auth-wrapper{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-flow:column nowrap;flex-flow:column nowrap;padding:2rem 1rem 2rem 1rem;width:100%;height:100%}.auth-wrapper .icon{font-size:80px;font-weight:800;color:#135058;cursor:default;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;padding:1rem 0 0 0;-webkit-user-select:none;-ms-user-select:none;user-select:none}.auth-wrapper h1{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-user-select:none;-ms-user-select:none;user-select:none;font-size:30px;color:#135058}.auth-wrapper form{display:-webkit-flex;display:flex;-webkit-flex-flow:column nowrap;flex-flow:column nowrap;-webkit-align-items:center;align-items:center;margin-top:1rem}.auth-wrapper form input{width:100%;padding:0.8rem 0.5rem 0.8rem 1rem;margin:0.5rem 0 0.5rem 0;background:rgba(255,255,255,0.6);border:3px solid #135058;display:-webkit-flex;display:flex;-webkit-flex-flow:column wrap;flex-flow:column wrap;-webkit-align-content:center;align-content:center;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-weight:700}.auth-wrapper form input:focus,.auth-wrapper form input :active{box-shadow:0px 3px 8px 0px rgba(0,0,0,0.75);transition:0.15s}.auth-wrapper form a{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;text-decoration:none;-webkit-align-items:self-end;align-items:self-end;font-weight:800;font-size:12px;margin-left:15rem;color:#135058}.auth-wrapper form .primary-button{margin:1.5rem 0 1rem 0;background:#135058}.auth-wrapper form .fp-button{margin-bottom:3rem}.auth-wrapper .google-button{background-image:url("https://upload.wikimedia.org/wikipedia/commons/5/53/Google_%22G%22_Logo.svg");background-repeat:no-repeat;background-position-y:center;background-position-x:5%;background-size:2rem;background-color:#fff;border:1px solid rgba(0,0,0,0.18);color:#000;font-family:'Roboto', sans-serif;font-weight:400}p{font-size:15px;font-weight:500;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-flow:column nowrap;flex-flow:column nowrap;-webkit-align-items:center;align-items:center;margin-bottom:1rem;cursor:default}a{font-weight:700;text-decoration:none;margin:0.1rem 0 0.1rem 0;-webkit-user-select:none;-ms-user-select:none;user-select:none;color:#135058}a:hover{text-decoration:none;color:#135058}.button{max-width:20rem;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;margin:0 auto 2.5rem auto;transition:0.25s;padding:0.9rem 0 0.9rem 0;border:none;font-weight:800;color:#fff;cursor:pointer}.button:hover{box-shadow:0px 3px 8px 0px rgba(0,0,0,0.75);transition:0.15s}@media only screen and (min-width: 0px) and (max-width: 767px){.auth-wrapper{padding:0;width:300px}.auth-wrapper .icon{font-size:50px}.auth-wrapper .form input{width:80%;padding:0.4rem 0.5rem 0.4rem 0.8rem}.auth-wrapper .form a{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;text-decoration:none;-webkit-align-items:self-end;align-items:self-end;font-size:12px;margin:0}.auth-wrapper .form .button{padding:0.8rem;width:80%}.auth-wrapper .google-button{padding:0.8rem;width:80%}}

canvas{border-bottom:3px solid #135058}h1{color:#135058}.toolbar-container{display:-webkit-flex;display:flex;-webkit-flex-flow:row wrap;flex-flow:row wrap;-webkit-justify-content:center;justify-content:center}.toolbar-container label{font-weight:600;margin-right:0.5rem}.toolbar-container input{margin-right:0.5rem;background:inherit;border:none}.toolbar-container .line-width{border:2px solid #135058}.toolbar-container .button-tools{padding-top:0.5rem;margin-bottom:1rem}button{padding:0.5rem;vertical-align:middle;transition:0.25s;border:none;border:2px solid #135058;font-weight:800;background-color:inherit;margin-left:0.2rem;cursor:pointer}button:hover{box-shadow:0px 3px 8px 0px rgba(0,0,0,0.75);transition:0.15s}button img{display:-webkit-flex;display:flex}.picture-list-wrapper{overflow:auto;max-width:100%;margin-bottom:1rem}.picture-list-wrapper .picture-list{display:-webkit-flex;display:flex;-webkit-flex-flow:column nowrap;flex-flow:column nowrap;-webkit-justify-content:center;justify-content:center;margin-bottom:0.2rem;padding:1rem}.picture-list-wrapper .picture-list img{border:1px solid #b3b3b3;max-width:100%;margin-bottom:0.3rem}footer{display:-webkit-flex;display:flex;-webkit-align-content:center;align-content:center;padding:1rem}.gallery-back-btn{margin-bottom:1rem}

